import { useState, useEffect } from "react";
import React from "react";

const CORECheckbox = props => {
  const [options, setOptions] = useState(props.options);
  const [selectedOptions, setSelectedOptions] = useState({});

  useEffect(() => {
    if (props.options) {
      setOptions(props.options);
    }
  }, [props.options]);

  const selectOptions = p => {
    const userSelectedPrivilege = Object.assign({}, selectedOptions);
    if (userSelectedPrivilege[p.name]) {
      delete userSelectedPrivilege[p.name];
      setSelectedOptions(userSelectedPrivilege);
    } else {
      userSelectedPrivilege[p.name] = p;
      setSelectedOptions(userSelectedPrivilege);
    }
  };

  useEffect(() => {
    if(Object.keys(selectedOptions).length){
      console.log(selectedOptions);
      props.callback(props.name, Object.keys(selectedOptions));
    }
  }, [selectedOptions])

  return (
    <>
    <div className="checkbox_container">
      <ul>
        {options.length
          ? options.map(option => {
              return (
                <li>
                  <div
                    className={`checkbox check_${
                      selectedOptions[option.name] ? true : false
                    }`}
                  >
                    <input
                      type="checkbox"
                      id={`claim_${option.name}`}
                      defaultChecked={
                        selectedOptions[option.name] ? true : false
                      }
                      checked={selectedOptions[option.name] ? true : false}
                      onChange={e => {
                        selectOptions(option);
                      }}
                    />
                    <label htmlFor={`claim_${option.name}`}> {option.name}</label>
                  </div>
                </li>
              );
            })
          : ""}
      </ul>
      </div>
    </>
  );
};

export default CORECheckbox;
